/* Sidebar Container */
.sidebar {
  display: f;
    width: 150px; /* Adjust the width as needed */
    background-color: #46494C; /* Background color of the sidebar */
    color: #fff; /* Text color */
    padding: 20px;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    overflow-y: auto; /* Add a scrollbar when content overflows */
  }
  .fakeSidebar{
    width: 150px; /* Adjust the width as needed */
    background-color: #46494C; /* Background color of the sidebar */
    color: #fff; /* Text color */
    padding: 20px;
    position: fixed;
    bottom: 0;
    right: 0;
    height: 100%;
    overflow-y: auto; /* Add a scrollbar when content overflows */
  }

  .maintitle{
    border: 10px solid rgb(25,133,161);
  }
  
  /* Sidebar Links (Menu Items) */
  .sidebar ul {
    list-style-type: none;
    padding: 0;
  }
  
  .sidebar ul li {
    padding: 15px 0;
    text-align: left;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .sidebar ul li:hover {
    background-color: #777; /* Background color on hover */
  }
  
  /* Category Links Section */
  .category-links {
    margin-top: 0px;
  }
  
  /* Category Links */
  .category-links a {
    display: block;
    color: #000;
    text-decoration: none;
    margin-bottom: 0px;
    transition: color 0.3s;
  }
  
