    color: #66d9ff; /* Link color on hover 
  } */

  .link-container{
    border: 5px solid #ccc;
    margin: 0 auto;
    background-color:rgb(210, 236, 237);
    max-width: 31%;
    text-align: center;
  }

  .text-container{
    border: 5px solid #ccc;
    padding: 20px;
    margin-left: 250px;
    margin-right: 250px;
    background-color: (220,220,221);
    max-width: 60%;
    text-align: left;
  }
  .centered {
    display: f;
    border: 7px solid(220,220,221);
    margin-left: 220px;
    max-width: 50%;
    text-align: left;
    padding-bottom: 20px;
